import React, { useState, useEffect } from 'react'
import {Panel, PanelHeader, HeaderButton, platform, IOS} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { ListItem, Button, Group, Div, Avatar } from '@vkontakte/vkui';
import Core from '../core/main.js';

const osname = platform();

const Default = props => {

  let [timer, setTimer] = useState(0);
  let [score, setScore] = useState(localStorage.getItem('score')) || 0;
  let [items, setItems] = useState(JSON.parse(localStorage.getItem('items')) || {
      farm: 0,
      factory: 0,
      mine: 0,
      country: 0
  });
  let [prices, setPrices] = useState(JSON.parse(localStorage.getItem('prices')) || {
    farm: { 
        price: 100,
        boost: 2 
    },
    factory: {
        price: 500,
        boost: 10
    },
    mine: {
        price: 2000,
        boost: 100
    },
    country: {
        price: 10000,
        boost: 500
    }
  });
  let [data, setData] = useState(JSON.parse(localStorage.getItem('data')) || {
      auto: 2,
      perClick: 1,
  });
  let [storage, setStorage] = useState(Array.from(items))

  const add = () => {
    saveScore(data.perClick);
  }

  // Cброс счёта
  const resetScore = (obj) => {
    for (var prop in obj) {
        if (obj.hasOwnProperty(prop)) {
            obj[prop] = { string: '', number: 0 }[typeof obj[prop]];
        }
    }
  }

  // Автоматическое добавление в секунду
  const autoAdd = () => {
    saveScore(data.auto);
    setTimer(!timer);
  }

  const getScore = () => {
      return parseInt(localStorage.getItem('score')) || 0;
  }

  const saveScore = ( additional ) => {
    setScore(getScore() + additional);
    localStorage.setItem('score', getScore() + additional);
  }

  const buy = (item) => {
    if(getScore() - prices[item].price > 0) {
        saveScore(-prices[item].price);
        setItems(Object.assign(items,{ [item]: items[item] + 1 }));
        setPrices(Object.assign(prices, { [item]: Object.assign(prices[item], { price: Math.round(prices[item].price * 1.1)})}));
        setData(Object.assign(data,{ auto: data.auto + prices[item].boost }));
        localStorage.setItem('items', JSON.stringify(items));
        localStorage.setItem('prices', JSON.stringify(prices));
        localStorage.setItem('data', JSON.stringify(data));
    } else {
        console.log('no money');
    }
  }

  console.log(storage);

  useEffect(() => {
    setInterval(() => {
        saveScore(data.auto);
    }, 1000);
  }, {});

    return (
      <Panel id={props.id}>
        		<PanelHeader>
                    Clicker
		        </PanelHeader>
        <Group>
            {Object.keys(items).map((item) => (
                <div>{ item }: - {items[item]} - Цена: {prices[item].price}</div>
            ))}
                <Div>
                    <button onClick={() => add()}>
                        Click +1
                    </button>
                </Div>
                <Div>
                    <button onClick={() => resetScore()}>
                        Сбросить счёт
                    </button>
                </Div>
                
                <Div>
                    <button onClick={() => buy('farm')}>
                        Ферма (+1/сек.)
                    </button>
                    <button onClick={() => buy('factory')}>
                        Ферма (+1/сек.)
                    </button>
                    <button onClick={() => buy('mine')}>
                        Ферма (+1/сек.)
                    </button>
                    <button onClick={() => buy('country')}>
                        Ферма (+1/сек.)
                    </button>
                </Div>
            </Group>
            { score }
      </Panel>
    )
}

export default Default;
